<template>
  <div id="AlbumLists">
    <div v-if="albums.length > 0" class="AlbumLists">
      <div v-for="(album,index) in albums" :key="album.a_id" class="Album">
        <div class="Album-image">
          <img
            v-bind:src="require(`../assets/images/${album.album_pic}`)"
            v-bind:alt="album.album_name"
          >
        </div>

        <div class="Album-info">
          <router-link :to="`/album/${album.a_id}`">
            <div class="Album-number">{{index+1}}</div>
            <div class="Album-name">{{album.album_name}}</div>
            <div class="Album-aritst">{{album.artist_name}}</div>
            <div class="Album-date">{{album.release_date}}</div>
            <div class="Album-genre">{{album.genres}}</div>
          </router-link>
        </div>
      </div>
    </div>
    <div v-else class="Album-empty">등록된 앨범이 없습니다.</div>
  </div>
</template>

<script>
// import albums from "./../albums.json";
import soundService from "@/services/soundService";
export default {
  name: "AlbumLists",
  data() {
    return {
      albums: []
    };
  },
  mounted() {
    this.getAlbums();
  },
  methods: {
    async getAlbums() {
      const response = await soundService.getAllAlbums();
      this.albums = response.data.albums;
    }
  }
};
</script>

<style>
.AlbumLists {
  width: 100%;
  max-width: 640px;
  padding: 30px 10px 120px 10px;
  margin: auto;
  text-align: left;
  box-sizing: border-box;
}
.AlbumLists > div:nth-of-type(2n) {
  margin-left: 18px;
}
.Album-empty {
  text-align: center;
  padding: 3rem 0;
}
.Album {
  width: calc(50% - 9px);
  display: inline-block;
  box-sizing: border-box;
  margin-bottom: 20px;
  text-align: center;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.07), 0 1px 5px 0 rgba(0, 0, 0, 0.1);
  background: #ffffff;
}
.Album-image img {
  width: 100%;
}
.Album-image img a {
  display: block;
}
.Album-info {
  padding: 15px 10px;
  position: relative;
  font-size: 1rem;
  display: inline-block;
}
.Album-info a {
  text-decoration: none;
}
.Album-number {
  position: absolute;
  top: 15px;
  left: 0;
  color: #000000;
  font-size: 1rem;
  line-height: 1.5rem;
}
.Album-name {
  min-width: 110px;
  color: #000000;
  font-size: 1rem;
  line-height: 1.5rem;
}
.Album-aritst {
  padding: 0.5rem 0;
  font-size: 1rem;
  color: #757271;
}
.Album-date {
  font-size: 0.7rem;
  line-height: 1rem;
  color: #757271;
}
.Album-genre {
  font-size: 0.7rem;
  line-height: 1rem;
  color: #757271;
}
</style>



